body {
  background: #2d3b36
    url(https://dzwonsemrish7.cloudfront.net/items/363k3L2V2a2Z3m3s460t/Background_wood_HD.jpg)
    no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  padding-top: 100px;
}
form,
textarea {
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  overflow: hidden;
}
form {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 333px;
  padding: 30px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0 0 13px 3px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 0 0 13px 3px rgba(0, 0, 0, 0.5);
  box-shadow: 0 0 13px 3px rgba(0, 0, 0, 0.5);
}
textarea {
  background: rgba(255, 255, 255, 0.4) no-repeat 16px 16px;
  width: 95%;
  height: 75%;
  border: 1px solid rgba(255, 255, 255, 0.6);
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  display: block;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 18px;
  color: #fff;
  padding-left: 45px;
  padding-right: 20px;
  padding-top: 12px;
  margin-bottom: 20px;
  resize: none;
}
::-webkit-input-placeholder {
  color: #fff;
}
:-moz-placeholder,
::-moz-placeholder {
  color: #fff;
}
:-ms-input-placeholder {
  color: #fff;
}
input:focus,
textarea:focus {
  background-color: rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0 0 5px 1px rgba(255, 255, 255, 0.5);
  -webkit-box-shadow: 0 0 5px 1px rgba(255, 255, 255, 0.5);
  box-shadow: 0 0 5px 1px rgba(255, 255, 255, 0.5);
  overflow: hidden;
}
.btn,
.result {
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
}
.btn {
  width: 138px;
  height: 44px;
  border: 1px solid #253737;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  background-color: #4f7c7a;
  background-image: -moz-linear-gradient(to bottom, #6da5a3 0, #416b68 100%);
  background-image: -o-linear-gradient(to bottom, #6da5a3 0, #416b68 100%);
  background-image: -webkit-linear-gradient(to bottom, #6da5a3 0, #416b68 100%);
  background-image: linear-gradient(to bottom, #6da5a3 0, #416b68 100%);
  float: left;
  color: #fff;
  cursor: pointer;
}
.result {
  margin-top: 3%;
  margin-left: auto;
  margin-right: auto;
  width: 70%;
  height: 333px;
  padding: 30px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0 0 13px 3px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 0 0 13px 3px rgba(0, 0, 0, 0.5);
  box-shadow: 0 0 13px 3px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  color: brown;
  resize: none;
}
.copystyle {
  font-size: 2em;
  text-align: center;
  text-shadow: 0 0 20px #000;
}
